    cursor: pointer;
}

.nav__menu {
    line-height: 45px;
    font-weight: 700;
    text-transform: uppercase;
}

.nav__menu-item {
    display: inline-block;
    position: relative;
}

.nav__menu-item:hover {
    background-color: #00aeef;
}

.nav__menu-item:hover .nav__submenu {
    display: block;
}

.nav__submenu {
    font-weight: 300;
    text-transform: none;
    display: none;
    position: absolute;
    width: 120px;
    background-color: #00aeef;
}

.nav__submenu-item {
    display: inline-block;
    position: relative;
    padding: 5px;
}

.nav__submenu-item:hover {
    background-color: #00aeef;
}

.nav__submenu-item:hover .nav__submenu_list {
    display: contents;
    left: 100%;
    top: 0px;
}

.nav__submenu_list {
    font-weight: 300;
    text-transform: none;
    display: none;
    position: absolute;
    width: 120px;
    background-color: #00aeef;
}

.nav__submenu-item_list:hover {
    color: rgba(0, 0, 0, 0.1);
}

.arrow-up {
    width: 0px;
    height: 0px;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #000;
    margin: 0 0 15px 0;
    position: absolute;
    top: 0px;
    right: 0;
}

@media only screen and (min-width: 600px) {
