    border-color: #2496ca #2496ca #1e7da9;
    height: 28px;
    width: 50px;
    border-radius: 4px;
    background-color: #28a7e1;
    box-shadow: inset 0 1px 0 hsla(0, 0%, 100%, .3);
    color: #fff;
    padding-bottom: 7%;
    padding-top: 3%;
    padding-left: 8%;
    padding-right: 8%;
    vertical-align: middle;
}

.pointer {
    cursor: pointer;
}

.second-nav-border {
    border-bottom: 1px solid #e4e3e3;
    padding-bottom: 1%;
}

.profile-image {
    background-color: #f1f0ee;
    border-radius: 50%;
    position: absolute;
    top: 170px;
    border: 7px solid white;
}

.suspend-button {
    width: 95%;
    border-radius: 5px;
    padding: 3%;
    color: white;
    background-color: #5ac5f1;
}

.ladies-button-mobile {
    background-color: #fd6680;
    color: white;
    border-radius: 5px;
    padding: 3%;
    width: 100%;
}

.ladies-button {
    background-color: #fd6680;
    color: white;
    border-radius: 5px;
    padding: 3%;
    width: 120%;
}

.view-profile_ex {
    background-color: white !important;
    color: #08acef !important;
}

.big-input {
    width: 95%;
    height: 60px;
    border-radius: 5px;
    border: 1px solid#f1f0ee;
}

.sky-a {
    color: skyblue;
}

.req-butt {
    border-radius: 5px;
    background-color: #8ec231;
    border: 1px solid #7baa17;
    padding: 2%;
    color: white;
}

.service-list-divs {
    text-align: right;
    border-bottom: 1px solid #bababa;
    padding: 1%;
    font-size: 12px;
}

.h-a {
    color: Black !important;
}

.small_tab_name {
    font-size: 9px;
}

.conf-padding {
    padding-top: 2%;
    padding-bottom: 2%;
}

.conf-inputs {
    width: 50%;
    height: 100%;
    border-radius: 5px;
    border: 1px solid #ddd;
}

.stat-buttons {
    width: 20%;
