    color: white;
    padding: 1%;
    height: auto;
    border-radius: 5px;
    font-size: 12px;
}

.center {
    text-align: center;
}

.points {
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #8a3808;
    height: 40px;
    margin-bottom: 15px;
    color: #fff;
    text-align: center;
    font-size: 14px
}

.gray {
    color: gray !important;
}

.transaction_buttons {
    padding: "5%";
    border: "1px solid cadetblue";
    width: "64px";
    border-radius: "4px";
    color: "green";
    background: "white";
}

.remove_pads {
    padding-left: 0px !important;
    padding-right: 0px !important;
}

::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    font-size: 12px;
    color: #bbbbbb !important;
}

::-moz-placeholder {
    /* Firefox 19+ */
    font-size: 12px;
    color: #bbbbbb !important;
}

:-ms-input-placeholder {
    /* IE 10+ */
    font-size: 12px;
    color: #bbbbbb !important;
}

:-moz-placeholder {
    /* Firefox 18- */
    font-size: 12px;
    color: #bbbbbb !important;
}

.adjust-to-page {
    min-height: 600px;
}

.standard_heading {
    font-size: 20px;
}

.fieldset_border {
    border: 1px solid #e4e4e4;
    display: block;
    margin-left: 2px;
    margin-right: 2px;
    padding-top: 0.35em;
    padding-bottom: 0.625em;
    padding-left: 0.75em;
    padding-right: 0.75em;
}

.fieldset_legend {
    font-size: 11px;
    text-align: left;
    margin-left: 10px;
    padding-left: 5px;
    margin-bottom: 0px;
}

.affiliator-links {
    border: 1px solid #e2e2e2;
    border-radius: 6px;
    font-size: 12px;
    padding: 6px
}

button:focus,
textarea:focus,
input:focus {
    outline: 0 !important;
}

.badge-style {
    width: 20px;
    height: 20px;
    border-radius: 50%;
}

.material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    /* -webkit-font-feature-settings: 'liga'; */
    -webkit-font-smoothing: antialiased;
}

.aff-dashboard-eligibilitybar {
    margin-bottom: 15px;
    background-color: #eeeeee;
    border-radius: 5px;
    height: 45px;
    padding: 10px
}

.dashboard-shadow {
    border-radius: 5px;
    box-shadow: 1px 1px #d0d0d0;
}

.dashboard-counters {
    color: white !important;
    border-radius: 4px;
    padding: 4px;
    box-shadow: 2px 2px gray
}

.tr-bottom {
    border-bottom: 1px solid gray
}

.personal-info-mobile {
    text-align: left;
    padding-right: 0px;
    font-size: 13px;
}

.top_page_cards{
    height: 650px;
    border: 1px solid #f1f1f1;
    border-radius: 6px;
}
.top_page_cards_mobile{
    height: 450px;
    border: 1px solid #f1f1f1;
    border-radius: 6px;
}
.content {
    min-height: calc(75vh - 80px);
  }
  .hide{
      display: none;
  }
  .show:hover + .hide{
      display: block;
  }
  .div_hover{
      width:100%;
  }
  .div_hover:hover{
      cursor: pointer;
      background-color: #ccc;
      width:100%;
  }
  .Cdiv{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 85%;
    /* display: inline-block; */
    font-weight: normal;    
}
